<template>
    <div id="blog-header">
        <nav>
            <ul>
              <li>
                <router-link to="/" exact>博客</router-link>
                <!-- <exact表示两个点击只有一个有颜色的变化 -->
                <!-- 显示是a标签 -->
                <router-link to="/add" exact>写博客</router-link>
              </li>
            </ul>
        </nav>
    </div>
</template>
<script>
export default {
    name:"blog-header"
    
}
</script>
<style scoped>
ul{
    list-style-type: none;
    text-align: center;
    margin: 0;
}
li{
    display: inline-block;
    margin: 0,10px;
}
a{
    color:#fff;
    padding: 12px;
    text-decoration: none;
    border-radius: 5px;
}
nav{
    background: crimson;
    padding:30px 0;
    margin-bottom: 40px;

}
.router-link-active{
    background:rgb(255,255,255,0.8);
    color:#444;
}
</style>





