<template>
  <div id="showblog" v-theme:column="'narrow'">
      <!-- column是binding参数 narrow是column是binding参数值 -->
      <p>博客总览</p>
      <input class="search" type="text" v-model="search" placeholder="搜索"/>
      <!-- <div v-for="(blog,index) in blogs" :key="index" class="single-blog"> -->
        <div v-for="(blog,index) in filterBlog" :key="index" class="single-blog">
             <!-- 遍历函数 -->
          <router-link v-bind:to="'/blog/'+blog.id">
          <!-- 将id传给singleblog singleblog用#router接收id-->
            <h2 v-rainbow>{{blog.title | touppercase}}</h2>
             <!-- 过滤器 传的值是title 打印出函数返回的值 -->
            <article>{{blog.content | snippet}}</article>
          </router-link>
          
          
       </div>
   
  </div>
</template>

<script>

export default {
  name: 'showblog',
  data(){
      return{
          blogs:[],
          search:""

      }
  },
  created(){
    //   this.$http.get("http://jsonplaceholder.typicode.com/posts") 本地json只能放在static文件夹中
      this.$http.get("https://myblog-demo.firebaseio.com/posts.json")
      .then(function(data){
          // console.log(data.json());
          return data.json()
        // console.log(data.body.slice(0,10))
        // this.blogs=data.body.slice(0,10);
        // 返回前10条数据
      })
      .then(function(data){
          console.log(data);
          var blogarray=[];
          for (let key in data){
            console.log(key);
            console.log(data[key]);
            // 拿到唯一标识
            data[key].id=key;
            blogarray.push(data[key]);
        }
        console.log(blogarray);
        this.blogs=blogarray;
        console.log(this.blogs);
      })
  },
  computed:{
      filterBlog(){
          return this.blogs.filter((blog)=>{
            //   匹配每一个blog
              return blog.title.match(this.search);
              //返回合适的blog
          })
      }
  },
//   局部过滤器
  filters:{
    touppercase(value){
      return value.toUpperCase();
    },
    snippet(value){
      return value.slice(0,100)+"...";
    }
  },
  //局部指令
  directives:{
      'rainbow':{
        bind(el,binding,vnode){
            el.style.color="#"+Math.random().toString(16).slice(2,8);
          }
        },
        "theme":{
          bind(el,binding,vnode){
              if(binding.value=='wide'){
                  el.style.maxWidth="1260px";
                  }
              else if(binding.value=='narrow'){
                  el.style.maxWidth="560px";
                  }
              if(binding.arg="column"){
                  el.style.background="#6677cc";
                  el.style.padding="20px";
                  }
            }
        }



  } 
}
</script>

<style scoped>
#showblog{
    max-width: 800px;;
    margin: 0 auto;
}
.single-blog{
    padding: 20px;
    margin:20px 0;
    box-sizing: border-box;
    background-color: #eee;
    border: 1px dotted #aaa;
}
.search{
    display: block;
    width:100%;
    height:30px;
    border: #eee;
}
#showblog a{
  color:#444;
  text-decoration: none;
}
input[type="text"]{
  padding:8px;
  width:100%;
  box-sizing: border-box;
  
}
</style>



