<template>
    <div id="singleblog">
        <h1>{{blog.title}}</h1>
        <article>{{blog.content}}</article>
        <P>作者:{{blog.author}}</P>
        <ul>
            <li v-for="(category,index) in blog.categories" :key="index">
                {{category}}
            </li>
        </ul>
    </div>
</template>
<script>
export default {
    name:"singleblog",
    data(){
        return{
            id:this.$route.params.id,
            // 首先跳转页面就会得到这个id
            // 然后传给create函数
            blog:{}
        }
    },
    created(){
        this.$http.get("https://myblog-demo.firebaseio.com/posts/"+this.id+".json")
        .then(function(data){
        //   console.log(data);
        // this.blog=data.body;
        // console.log(this.$route.params.id);
        return data.json();
        
      })
      .then(function (data){
          this.blog=data;

      })
    }
}
</script>
<style scoped>
#singleblog{
    max-width: 960px;
    margin:0 auto;
    padding: 20px;
    background: #eee;
    border: 1px dotted #aaa;
}
</style>




