<template>
  <div id="add-blog">
   <h2>添加博客</h2>
   <form v-if="!submited">
     <label>博客标题</label>
     <input type="text" v-model="blog.title" required/>
     <label>博客内容</label>
     <textarea v-model="blog.content"></textarea>
     <div id="checkbox">
      <label>Vue.js</label>
      <input type="checkbox" value="Vue.js" v-model="blog.categories"/>
      <!-- 将values值传到categories数组中 -->
      <label>node.js</label>
      <input type="checkbox" value="node.js"  v-model="blog.categories"/>
      <label>aaa.js</label>
      <input type="checkbox" value="aaa"  v-model="blog.categories"/>
     </div>
     <label>作者：</label>
     <select v-model="blog.author">
       <option v-for="(author,index) in authors" :key="index">
         {{author}}
       </option>
     </select>
     <button v-on:click.prevent="post">添加博客</button>
     <!-- 防止页面刷新 -->
   </form>

   <div v-if="submited">
     <h3>您的博客发布成功</h3>
   </div>

   <div id=#preview>
     <p>博客标题:{{blog.title}}</p>
     <p>博客内容:{{blog.content}}</p>
     <p>博客分类</p>
     <ul>
       <li v-for="(category,index) in blog.categories" :key="index">
         {{category}} 
        </li>
     </ul>
     <p>作者：{{blog.author}}</p>
   </div>

 </div>
</template>
// <script src="https://www.gstatic.com/firebasejs/5.8.4/firebase.js"></script>
<script>
// import firebase from 'firebase'
//   // Initialize Firebase
//    var config = {
//     apiKey: "AIzaSyB2Dr3wrol7t5pzmjNmvMCnv95pYnmfaxU",
//     authDomain: "myblog-demo.firebaseapp.com",
//     databaseURL: "https://myblog-demo.firebaseio.com",
//     projectId: "myblog-demo",
//     storageBucket: "myblog-demo.appspot.com",
//     messagingSenderId: "856173005683"
//   };
//   firebase.initializeApp(config);
export default {
  name: 'add-blog',
  data(){
    return{
      blog:{
        title:"",
        content:"",
        categories:[],
        author:""
      },
      authors:["YL","AL","ZK"],
      submited:false,
    }
  },
  methods:{
    post(){
      this.$http.post("https://myblog-demo.firebaseio.com/posts.json",this.blog)
      .then(function(data){
        console.log(data),
        this.submited=true;
      });
    }
  }
}
</script>

 <style scoped>

#add-blog *{
  box-sizing: border-box
  /* add-blog下的所有内容都是box-siziing 的展示 */
}
#add-blog{
  margin:20px auto;
  max-width: 500px;
  padding: 20px;
}
label{
  display: block;
  /* label独占一行 */
  margin: 10px auto;
}
input[type="text"],textarea,select{
  display: block;
  width:100%;
  padding: 8px;
}
#checkbox label{
  display: inline-block;
  margin-top: 2px;
}
#checkbox input{
  display: inline-block;
  margin-right: 10px;
}
button{
  display: block;
  margin: 20px 0;
  columns: #fff;
  border: 0;
  padding: 14px;
  border-radius: 4px;
  font-size: 18px;
  cursor: pointer;
}
#preview{
  padding: 10px 20px;
  border:1px dotted #ccc;
  margin:30px 0;
}
h3{
  margin-top: 10px;
}
textarea{
  height: 200px;
}
</style>







